import { Component, OnInit } from '@angular/core'; import { Router, ActivatedRoute } from '@angular/router'; import { Subscription } from 'rxjs/Subscription';  import { Config_envio_indicadores } from '../../../modelo/Config_envio_indicadores'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual'; import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'config_envio_indicadores-dados-details',   templateUrl: 'config_envio_indicadores_dadosDetails.component.html' }) export class Config_envio_indicadoresDadosDetailsComponent implements OnInit {    config_envio_indicadores: Config_envio_indicadores;    private sub: Subscription;    private errorMessage: string;    constructor(private route: ActivatedRoute, private pesquisaService: PesquisaService) { }    ngOnInit() {       this.sub = this.route.parent.params.subscribe(params => {         let o : ObjetoVirtual = new ObjetoVirtual();         o.classeJava = "br.com.primum.modelo.config_envio_indicadores.Config_envio_indicadores";                 let id = +params['id'];         this.config_envio_indicadores = new Config_envio_indicadores();         this.config_envio_indicadores.cd_config_envio_indicadores = id;         o.objetoJson = this.config_envio_indicadores;          this.pesquisaService.efetuarPesquisa(o)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);       });   }    onEfetuarPesquisa(result : any) {     if (!result) {       this.errorMessage = 'Houve um erro ao pesquisa a config_envio_indicadores. '; }      this.config_envio_indicadores = result;   }    ngOnDestroy() {     this.sub.unsubscribe();   } }


